import { AlertService } from './../../services/alert.service';
import { Subscription, of } from 'rxjs';
import { ProviderGroup } from './../../models/provider-group';
import { ActivatedRoute } from '@angular/router';
import { EnvService } from 'src/app/services/env.service';
import { ProviderService } from 'src/app/services/provider.service';
import { User } from './../../models/user';
import { Provider } from './../../models/provider';
import { Component, OnInit } from '@angular/core';
import { NavController, IonRefresher } from '@ionic/angular';
import { catchError } from 'rxjs/operators';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-adm-providers',
  templateUrl: './adm-providers.page.html',
  styleUrls: ['./adm-providers.page.scss'],
})
export class AdmProvidersPage implements OnInit {

  public providers: Provider[];
  public getName = User.getName;
  providersSubscription:Subscription;
  searchNameParam:string ="";
  refresher:IonRefresher;
  constructor(
    public providerService: ProviderService,
    private navController: NavController,
    public env: EnvService,
    private route:ActivatedRoute,
    private alert:AlertService
  ) {}
  navigateToProvider(provider: Provider) {
    this.providerService.setSelectedProvider(provider);

    this.navController.navigateForward(["provider/"+provider.id]);
  }
 
  ngOnInit() {
    this.providerService.allProvidersShared.getUpdateNotifier(this).subscribe(()=>{
      
      if(this.refresher)
      {
        this.refresher.complete();
      }
    })
    this.providerService.allProvidersShared.request();
    this.providerService.allProvidersShared.setFilter(p=>{
      return User.getName(p.user).includes(this.searchNameParam.trim());})
    
  }
  
  refresh(event)
  {
    this.refresher = event.target;
    this.providerService.allProvidersShared.request();
  }
  ngOnDestroy(): void {
    
  }
  
}
